<template>
  <div id="loading-container" class="vs-con-loading__container">
    <div class="alert-container" v-if="alertActive">
      <vs-alert :active.sync="alertActive" :color="alertVariant" closable icon-pack="feather" close-icon="icon-x" class="mb-5">
        <p>{{ alertMessage }}</p>
      </vs-alert>
    </div>

    <nv-card>
      <form-wizard ref="wizard"
                   color="rgba(var(--vs-primary), 1)"
                   :title="null" :subtitle="null"
                   :nextButtonText="$t('NEXT')"
                   :backButtonText="$t('PREVIOUS')">
        <template slot="custom-buttons-left" slot-scope="props">
          <vs-button class="mr-2" color="primary" type="border" @click="cancel" v-if="props.activeTabIndex === 0">{{ $t('CANCEL') }}</vs-button>
        </template>
        <template slot="finish">
          <div class="dropdown-button-container">
            <vs-button class="btnx" type="filled" @click="modifyBranch('browse')">{{ $t('SUBMIT') }}</vs-button>
            <vs-dropdown vs-trigger-click>
              <vs-button class="btn-drop" type="filled" icon="fa-angle-down" icon-pack="far"></vs-button>
              <vs-dropdown-menu>
                <vs-dropdown-item @click="modifyBranch('view')">
                  <span>{{ $t('SUBMIT_VIEW') }}</span>
                </vs-dropdown-item>
              </vs-dropdown-menu>
            </vs-dropdown>
          </div>
        </template>

        <tab-content :title="$t('GENERAL')" icon="far fa-file-alt">
          <div class="nv-row mb-4">
            <div class="nv-col sm:w-1/2 w-full required">
              <label class="vs-input--label">{{ $t('BRANCH_NAME') }}</label>
              <vs-input class="w-full" v-model="branch.branchName"/>
            </div>
            <div class="nv-col sm:w-1/2 w-full required">
              <label class="vs-input--label">{{ $t('BANK') }}</label>
              <bank-lookup :title="$t('BANK')" :recordValue="branch.bankId" :recordDisplay="branch.bankName" valueField="bankId" displayField="bankName" @select-record="selectBankRecord"/>
            </div>
          </div>
          <div class="nv-row mb-4">
            <div class="nv-col sm:w-1/2 w-full">
              <label class="vs-input--label">{{ $t('BRANCH_CODE') }}</label>
              <vs-input class="w-full" v-model="branch.branchCode"/>
            </div>
            <div class="nv-col sm:w-1/2 w-full">
              <label class="vs-input--label">{{ $t('RECORD_KEY') }}</label>
              <vs-input class="w-full" v-model="branch.recordKey"/>
            </div>
          </div>
          <div class="nv-row mb-4">
            <div class="nv-col sm:w-1/2 w-full">
              <label class="vs-input--label">{{ $t('SWIFT_CODE') }}</label>
              <vs-input class="w-full" v-model="branch.swiftCode"/>
            </div>
            <div class="nv-col sm:w-1/2 w-full">
              <label class="vs-input--label">{{ $t('LOCAL_CODE') }}</label>
              <vs-input class="w-full" v-model="branch.localCode"/>
            </div>
          </div>
          <div class="nv-row mb-4">
            <div class="nv-col w-full">
              <label class="vs-input--label">{{ $t('EXTERNAL_ID') }}</label>
              <vs-input class="w-full" v-model="branch.externalBranchId"/>
            </div>
          </div>
          <div class="nv-row mb-4">
            <div class="nv-col w-full">
              <vs-checkbox v-model="branch.acceptsPayments">{{ $t('ACCEPTS_PAYMENTS') }}</vs-checkbox>
            </div>
          </div>
          <div class="nv-row mb-4">
            <div class="nv-col w-full">
              <vs-checkbox v-model="branch.disabled">{{ $t('DISABLED') }}</vs-checkbox>
            </div>
          </div>
        </tab-content>

        <tab-content :title="$t('ADDRESS')" icon="far fa-map">
          <div class="nv-row mb-4">
            <div class="nv-col sm:w-1/2 w-full">
              <label class="vs-input--label">{{ $t('ADDRESS_1') }}</label>
              <vs-input class="w-full" v-model="branch.address1"/>
            </div>
            <div class="nv-col sm:w-1/2 w-full">
              <label class="vs-input--label">{{ $t('ADDRESS_2') }}</label>
              <vs-input class="w-full" v-model="branch.address2"/>
            </div>
          </div>
          <div class="nv-row mb-4">
            <div class="nv-col sm:w-1/2 w-full">
              <label class="vs-input--label">{{ $t('DISTRICT') }}</label>
              <vs-input class="w-full" v-model="branch.district"/>
            </div>
            <div class="nv-col sm:w-1/2 w-full">
              <label class="vs-input--label">{{ $t('CITY') }}</label>
              <vs-input class="w-full" v-model="branch.city"/>
            </div>
          </div>
          <div class="nv-row mb-4">
            <div class="nv-col sm:w-1/2 w-full">
              <label class="vs-input--label">{{ $t('PROVINCE') }}</label>
              <vs-input class="w-full" v-model="branch.province"/>
            </div>
            <div class="nv-col sm:w-1/2 w-full required">
              <label class="vs-input--label">{{ $t('COUNTRY') }}</label>
              <country-lookup :title="$t('COUNTRY')" :recordValue="branch.countryGeoId" :recordDisplay="branch.countryName" valueField="geoId" displayField="geoName" @select-record="selectCountryRecord"/>
            </div>
          </div>
          <div class="nv-row mb-4">
            <div class="nv-col sm:w-1/2 w-full">
              <label class="vs-input--label">{{ $t('POSTAL_CODE') }}</label>
              <vs-input class="w-full" v-model="branch.postalCode"/>
            </div>
            <div class="nv-col sm:w-1/2 w-full">
              <label class="vs-input--label">{{ $t('POSTAL_CODE_EXT') }}</label>
              <vs-input class="w-full" v-model="branch.postalCodeExt"/>
            </div>
          </div>
          <div class="nv-row">
            <div class="nv-col sm:w w-full">
              <label class="vs-input--label">{{ $t('PO_BOX') }}</label>
              <vs-input class="w-full" v-model="branch.poBox"/>
            </div>
          </div>
        </tab-content>

        <tab-content :title="$t('FUNCTIONS')" icon="far fa-check">
          <ul class="flex justify-start">
            <li class="mr-5" v-for="(bankFunction, index) in bankFunctionList" :key="index">
              <vs-checkbox v-model="functions" :vs-value="bankFunction.functionId" >
                <i class="text-primary mr-1" :class="bankFunction.iconClass"></i> <span :class="disabledClass(bankFunction)">{{ bankFunction.functionName }}</span>
              </vs-checkbox>
            </li>
          </ul>
        </tab-content>
      </form-wizard>
    </nv-card>
  </div>
</template>

<script>
import AlertMixin from '@/mixins/commons/AlertMixin'
import DisabledClassMixin from '@/mixins/commons/DisabledClassMixin'
import LoadingMixin from '@/mixins/commons/LoadingMixin'
import BranchService from '@/services/bank/BranchService'
import BankLookup from '@/views/bank/bank/BankLookup'
import CountryLookup from '@/views/nucleus/country/CountryLookup'
import BankFunctionService from '@/services/bank/BankFunctionService'
import { FormWizard, TabContent } from 'vue-form-wizard'

export default {
  name: 'branch-modify',
  mixins: [AlertMixin, DisabledClassMixin, LoadingMixin],
  components: {
    FormWizard,
    TabContent,
    BankLookup,
    CountryLookup
  },
  data: function () {
    return {
      branch: {},
      bankFunctionList: [],
      functions: []
    }
  },
  methods: {
    async load () {
      let self = this
      BranchService.branches({
        branchId: this.$route.params.id,
        orderByField: 'branchName'
      }).then(function (response) {
        self.branch = response.data.branchList[0] || {}
        if (Object.getOwnPropertyNames(self.branch).length > 1) {
          self.branch.acceptsPayments = self.branch.acceptsPayments === 'Y'
          self.branch.disabled = self.branch.disabled === 'Y'
        }
        self.getBranchServices()
      })

      BranchService.branchFunctions({
        branchId: this.$route.params.id
      }).then(function (response) {
        let branchFunctionList = response.data.branchBankFunctionList || []
        for (let i = 0; i < branchFunctionList.length; i++) {
          self.functions.push(branchFunctionList[i].functionId)
        }
      })
    },
    getBranchServices () {
      let self = this
      BankFunctionService.bankFunctions({
      }).then(function (response) {
        self.bankFunctionList = response.data.functionList || []
      })
    },
    selectBankRecord (record) {
      this.branch.bankId = record.bankId || ''
    },
    selectCountryRecord (record) {
      this.branch.countryGeoId = record.geoId || ''
    },
    modifyBranch (nextAction) {
      let self = this
      this.startLoading('loading-container')
      BranchService.modifyBranch({
        branchId: this.$route.params.id,
        branchName: this.branch.branchName,
        countryGeoId: this.branch.countryGeoId,
        bankId: this.branch.bankId,
        branchCode: this.branch.branchCode,
        swiftCode: this.branch.swiftCode,
        externalBranchId: this.branch.externalBranchId,
        localCode: this.branch.localCode,
        recordKey: this.branch.recordKey,
        address1: this.branch.address1,
        address2: this.branch.address2,
        district: this.branch.district,
        city: this.branch.city,
        province: this.branch.province,
        postalCode: this.branch.postalCode,
        postalCodeExt: this.branch.postalCodeExt,
        poBox: this.branch.poBox,
        functions: this.functions.join(','),
        acceptsPayments: this.branch.acceptsPayments ? 'Y' : 'N',
        disabled: this.branch.disabled ? 'Y' : 'N'
      }).then(function (response) {
        self.stopLoading('loading-container')
        if (response.status === 200) {
          if (nextAction === 'view') {
            self.$router.push({ name: 'ViewBranch', params: { id: response.data.branchId } })
          } else {
            self.$router.push({ name: 'BrowseBranches' })
          }
        }
      }).catch(function (error) {
        self.stopLoading('loading-container')
        self.showDangerAlert(error.response.data.errors)
      })
    },
    cancel () {
      this.$router.back()
    }
  },
  mounted () {
    this.load()
  }
}
</script>
